@import url(https://fonts.googleapis.com/css?family=Caveat:400,700);
.login-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  min-height: 60vh;
  flex-direction: column;
  position: relative; }

.login-btn:hover {
  background: #D0214F !important;
  color: #fff !important; }

.login-form {
  box-shadow: 0 0.5px 0 0 #ffffff inset, 0 1px 2px 0 #B3B3B3;
  padding: 40px; }

.login-form {
  display: flex;
  align-items: flex-end; }

.ui.attached.negative.message, .ui.negative.message {
  position: absolute;
  top: 3px;
  right: 30px;
  min-width: 218px; }

.ui.attached.info.message, .ui.info.message {
  margin-bottom: 7px; }

.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 85vh;
  margin-left: -115px; }
  .form-options .form-options-selected {
    width: 65%; }
  .form-options .form-border {
    border: 3px solid;
    padding: 25px; }
  .form-options input[type=number]::-webkit-inner-spin-button,
  .form-options input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; }

.navbar {
  display: flex;
  justify-content: space-between;
  margin-left: 135px;
  margin-right: 135px;
  margin-top: 20px; }
  .navbar .navbar-start {
    display: flex;
    width: 20%;
    align-items: center; }
    .navbar .navbar-start .navbar-logo {
      cursor: pointer; }
    .navbar .navbar-start .navbar-greeting h2 {
      margin-left: 20px;
      font-size: 36px;
      font-family: 'Caveat', cursive; }
  .navbar .logout-btn {
    height: 45px !important; }
  .navbar .hide {
    display: none; }
  .navbar .show {
    display: block; }

